@use "variables" as v;
@use "mixins" as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  font-family: v.$main_font;
  font-size: 1.6rem;
}

main {
  background-color: v.$dLight;
}
.container {
  margin: 0 auto;
  max-width: 100%;
  @include m.tablet {
    max-width: 75%;
  }
}

p {
  font-size: 2rem;
  color: v.$black;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}
