html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

main {
  background-color: #eaf9ee;
}

.container {
  margin: 0 auto;
  max-width: 100%;
}
@media (min-width: 768px) {
  .container {
    max-width: 75%;
  }
}

p {
  font-size: 2rem;
  color: #000;
  margin: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  height: 280px;
  background-image: url(../../../build/img/header-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  color: #fff;
  align-items: center;
}
@media (min-width: 992px) {
  .header {
    height: 320px;
  }
}
.header h1 {
  font-size: 6rem;
  margin-bottom: 7.5rem;
}
@media (min-width: 992px) {
  .header h1 {
    font-size: 8.5rem;
  }
}
.header p {
  text-align: center;
  width: 100%;
  background-color: #547980;
  color: #eaf9ee;
  padding: 2rem 0;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .header p {
    font-size: 2rem;
  }
}

main {
  padding: 4rem 0;
}

.form {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .form {
    border-radius: 2rem;
    box-shadow: 0px 0px 20px -11px rgba(0, 0, 0, 0.75);
  }
}
.form:first-child {
  margin-bottom: 4rem;
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 2.5rem;
}
.title h2 {
  font-size: 3rem;
}
.title h3 {
  font-size: 2rem;
  color: #594f4f;
}
@media (min-width: 768px) {
  .title {
    display: flex;
    justify-content: center;
    gap: 1rem;
    align-items: baseline;
  }
}

.entry-data {
  padding: 1.5rem 3rem 2rem 3rem;
  width: 100%;
  display: grid;
  align-items: center;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 2rem;
  row-gap: 1rem;
}
.entry-data .entry-result {
  width: 100%;
  grid-column: span 2;
  display: flex;
  gap: 1rem;
  justify-content: center;
}
.entry-data .entry-result p {
  font-size: 1.6rem;
}
.entry-data .entry-result p:last-child {
  font-weight: bold;
}
@media (min-width: 768px) {
  .entry-data .entry-result {
    grid-column: span 1;
    flex-direction: column;
    text-align: end;
  }
}

.btn {
  width: 100%;
  border: none;
  border-radius: 1rem;
  padding: 1.2rem 2.2rem;
  color: #fff;
  box-shadow: 0px 0px 20px -11px rgba(0, 0, 0, 0.75);
  background-color: #547980;
}
.btn:active {
  background-color: #547980;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
@media (min-width: 992px) {
  .btn:hover {
    cursor: pointer;
    transition: all 400ms;
    transform: scale(1.02);
  }
}

.apend-container {
  width: 100%;
  display: flex;
  gap: 2rem;
  justify-content: space-evenly;
  margin-top: 3rem;
}

.btn-calculate {
  background-color: #69a18e;
}
@media (min-width: 992px) {
  .btn-calculate:hover {
    background-color: #538473;
  }
}

.input {
  width: 100%;
}
.input input,
.input select {
  width: 100%;
  margin-top: 0.6rem;
  border: none;
  border-radius: 0.5rem;
  box-shadow: 0px 0px 20px -11px rgba(0, 0, 0, 0.75);
}
.input label {
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .input label {
    font-size: 1.6rem;
  }
}
.input input {
  padding: 1rem;
}
.input select {
  padding: 0.75rem;
}

.result {
  text-align: center;
  display: flex;
  flex-direction: column;
}
.result h4 {
  margin: 0 0 1.2rem 0;
  font-size: 1.8rem;
}
.result p {
  font-size: 2.3rem;
  font-weight: bold;
}

.form-ingest {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #88d0cc;
}
@media (min-width: 768px) {
  .form-ingest .entry-data {
    grid-template-columns: 3fr 3fr 2fr;
    gap: 2rem;
  }
}

.ingest:nth-child(odd) {
  background-color: #52bab5;
}
.ingest:nth-child(even) {
  background-color: #addedc;
}

.ingest-result {
  width: 100%;
  padding: 0 3rem;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .ingest-result {
    flex-direction: row;
    justify-content: center;
    align-items: baseline;
    gap: 3rem;
  }
}

.form-water {
  background-color: #8cadb3;
}
.form-water .initial-ask,
.form-water .dialisis,
.form-water .water-calculator {
  padding-right: 3rem;
  padding-left: 3rem;
}
@media (min-width: 768px) {
  .form-water .entry-data {
    grid-template-columns: 2fr 2fr 1fr;
    gap: 2rem;
  }
}

.initial-ask {
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.initial-ask .btns-yes-no {
  display: flex;
  justify-content: center;
  gap: 3rem;
}
.initial-ask .btn {
  width: 100%;
}
@media (min-width: 768px) {
  .initial-ask .btn {
    width: 10rem;
  }
}
.initial-ask .bg-green {
  background-color: #69a18e;
}
.initial-ask .bg-red {
  background-color: #a35d5d;
}

.dialisis:nth-child(odd) {
  background-color: #83a6ad;
}
.dialisis:nth-child(even) {
  background-color: #a1bcc2;
}

.yesterday-dialisis {
  padding-top: 1.5rem;
  padding-bottom: 2rem;
}
.yesterday-dialisis .last-day {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3rem;
  place-items: center;
}
.yesterday-dialisis .last-day label {
  width: 100%;
  text-align: end;
}

.dialisis-result {
  margin: 3rem 0;
}
@media (min-width: 768px) {
  .dialisis-result {
    flex-direction: row;
    justify-content: center;
    align-items: baseline;
    gap: 3rem;
  }
}

.water-calculator {
  display: grid;
  align-items: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .water-calculator {
    grid-template-columns: repeat(2, 1fr);
  }
}

.inputs-container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.water-result {
  background-color: #addedc;
  border-radius: 1.5rem;
  padding: 1.5rem 0;
  align-items: center;
}
@media (min-width: 768px) {
  .water-result {
    padding: 3rem 0;
  }
}